@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f6f6f0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



.header-container {
    font-family: 'Playfair Display', serif; 
    text-align: center;
}

.newspaper-name {
    margin-bottom: 0;
    font-size: 3rem;
    font-weight: 900;
    cursor: pointer;
}

.header-date {
    margin-top: 0.5rem;
    margin-bottom: 0rem;
    padding-bottom: 1.5rem;
    font-size: 1.5rem;
}

.header-category-container {
    border-bottom: 1px solid black;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: goldenrod;
    /* position: sticky;
    top: 0px; */
    z-index: 20;
}

.header-category {
    margin: 0.25rem 0rem;
    color: white;
    text-decoration: none;
    font-style: italic;
    width: 175px;
    text-align: center;
    border: none;
    background-color: transparent;
    transition: letter-spacing 1s;
}

.header-category:focus{
    color: black
}
.header-category:hover{

    cursor: pointer;
    letter-spacing: 0.15rem;

}

.nav-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    border-top: 1px solid darkslategray;
    border-bottom: 1px solid darkslategray;
    background-color: #f6f6f0;
    font-family: 'Playfair Display', serif; 
    text-align: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
}

.nav-link{
    width: 200px;
    font-weight: 700;
    font-size: 1.05rem;
    color: darkgoldenrod;
 
}

.nav-link:hover {
    color: white;
    background-color: goldenrod;
}
.nav-link:focus {
    color: black;
}

.upper-header-container {
   background-color: #f6f6f0;
}

.header-social-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.social-btn {
    margin: 0rem 0.75rem 0.5rem 0.75rem;
    font-size: 1.25rem;
    border: none;
    background: transparent;
    color: black;
}

.twitter-header-btn{
    color: #1DA1F2;
}

.github-header-btn {
    color: purple;
}

.website-header-btn {
    color: black;
}

.linkedin-header-btn {
    color: #0967C2
}

.twitter-header-btn:hover, .github-header-btn:hover,.website-header-btn:hover, .linkedin-header-btn:hover {
    color: darkgoldenrod;
}

.newspaper-headin-navlink {
    color: black;
    text-decoration: none;
}

.newspaper-headin-navlink:hover {
    color: black;
}

.ownblog-card-container{
    width: 500px;
    padding: 1rem;
    margin: 1.5rem auto;
    border: 1px solid black;
    background-color:#FFFFFF;
    transition: -webkit-transform 1s ease;
    transition: transform 1s ease;
    transition: transform 1s ease, -webkit-transform 1s ease;
}

.ownblog-card-container:hover {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    border: 1px solid goldenrod;
}

.ownblog-edit-btn-container{
    display: flex;
    justify-content: space-around;
}

.ownblog-btn {
    width: 200px;
    margin: 1rem 0rem;
}

.no-current-drafts-heading {
    font-family: "Noto Mono Sans";
    color: tomato;
    text-align: center;
    margin-top: 3rem;
    font-weight: 900;

    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
    width: 100%;
    max-width: 1000px;
}



.blog-card-container {
    width: 500px;
    padding: 1rem;
    margin: 1.5rem auto 0rem auto;
    border: 1px solid black;
    overflow: hidden;
    background-color:hsl(0, 0%, 100%);
    color: black;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}
.blog-card-container:hover {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    border: 1px solid darkgoldenrod;
}

.nav-link-container {
    text-decoration: none;
}

.blog-card-img-container {
    width: 100%;
    height: 250px;
    overflow: hidden;
}

.blog-card-text-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
    margin-top: 1rem;
    padding: 0rem 0.5rem;
}

.blog-card-img {
    width: 475px;
    height: 300px;
}

.blog-card-title {
    font-family: 'Playfair Display', serif;
    font-size: 1.5rem;
    font-weight: 900;
}

.blog-card-category {
    margin:0;
    font-family: 'Lato', sans-serif;
    text-decoration: none;
    font-style: italic;
}

.blog-card-author {
    font-family: 'Lato', sans-serif;
}
.blog-post-list-container {

}
.aside-container {
    width: 300px; 
    margin: 1.5rem auto 0rem auto;
    border: 1px solid black;
    padding: 1rem;
    background-color:#FFFFFF;
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
}

.aside-heading {
    font-family: 'Playfair Display', serif;
    font-weight: 700;
}
.aside-item-container {
    display: flex;
    width: 100%;
    height: 100px;
    margin-top: 1rem;
    border-top: 2px solid black;
    border-bottom: 2px solid black;
    background-color: #ffffff;
}

.aside-item-img-container {
    display: flex;
    align-items: center;
    width: 75px;
    height: 100px;

}

.aside-item-img {
    width: 75px;
    height: 75px;
    object-fit:fill;
}

.aside-item-text-container {
    padding: 0.5rem 0.5rem;
    text-align: left;
}

.aside-item-category {
    font-size: 11px;
    margin: 0;
    font-family: "Lato", sans-serif;
}

.aside-item-title {
    font-size: 14px;
    text-align: left;
    font-family: "Playfair Display", serif;
    font-weight: 700;
}

.aside-item-author {
    font-size: 11px;
    font-family: "Lato", sans-serif;
}

.aside-item-navlink {
    text-decoration: none;
    color: black;
}

.aside-item-navlink:hover {
    color: rgb(141, 108, 23);
}

.blogform-container {
    width: 575px;
    background: #FFFFFF;
    padding: 2rem 3rem;
    margin: 2rem auto;
    border: 2px solid goldenrod;
}

.blogform-heading {
    color: goldenrod;
    font-family: 'Playfair Display', serif; 
}

.blogform-container .blogform-btn {
    width: 200px;
}

.form-control {
    border: 1px solid goldenrod;
    padding: 0.5rem;
}


.main-container {
    display: flex;
    justify-content: space-around;
    background-color: #f6f6f0;
    margin: 0 auto;
}


.profile-container {
    text-align: left;
    width: 150px;
    margin: 0 auto;
    margin-top: 1rem;
    width: 355px;
    background: #ffffff;
    padding: 2rem 3rem;
    margin: 2rem auto;
    border: 2px solid goldenrod;
}


.profile-details-heading {
    color: goldenrod;
    font-family: "Playfair Display", serif;
    }


.profile-details-subheading {
    font-size: 1.25rem;
    font-weight: 700;
}

.profile-container .profile-btn {
    margin: 0rem 0.75rem;
    font-family: "Playfair Display", serif;
    font-weight: 700;
    width: 200px;
}


.registration-container {
    width: 450px;
    background: #FFFFFF;
    padding: 2rem 3rem;
    margin: 2rem auto;
    border: 2px solid goldenrod;
}

.registration-heading {
    color: goldenrod;
    font-family: 'Playfair Display', serif; 
}

.login-container {
    width: 275px;
    background: #FFFFFF;
    padding: 2rem 3rem;
    margin: 2rem auto;
    border: 2px solid goldenrod;
}

.login-heading {
    color: goldenrod;
    font-family: 'Playfair Display', serif; 
}

.btn-goldenrod { 
    color: #FFFFFF; 
    background-color: #D4A11F; 
    border-color: #FFFFFF; 
    font-weight: 700;
    font-family: 'Playfair Display', serif; 
    width: 100px;
  } 
   
  .btn-goldenrod:hover, 
  .btn-goldenrod:focus, 
  .btn-goldenrod:active, 
  .btn-goldenrod.active, 
  .open .dropdown-toggle.btn-goldenrod { 
    color: #FFFFFF; 
    background-color: #C78F00; 
    border-color: #FFFFFF; 
  } 
   
  .btn-goldenrod:active, 
  .btn-goldenrod.active, 
  .open .dropdown-toggle.btn-goldenrod { 
    background-image: none; 
  } 
   
  .btn-goldenrod.disabled, 
  .btn-goldenrod[disabled], 
  fieldset[disabled] .btn-goldenrod, 
  .btn-goldenrod.disabled:hover, 
  .btn-goldenrod[disabled]:hover, 
  fieldset[disabled] .btn-goldenrod:hover, 
  .btn-goldenrod.disabled:focus, 
  .btn-goldenrod[disabled]:focus, 
  fieldset[disabled] .btn-goldenrod:focus, 
  .btn-goldenrod.disabled:active, 
  .btn-goldenrod[disabled]:active, 
  fieldset[disabled] .btn-goldenrod:active, 
  .btn-goldenrod.disabled.active, 
  .btn-goldenrod[disabled].active, 
  fieldset[disabled] .btn-goldenrod.active { 
    background-color: #D4A11F; 
    border-color: #FFFFFF; 
  } 
   
  .btn-goldenrod .badge { 
    color: #D4A11F; 
    background-color: #FFFFFF; 
  }

.blog-detail-card-container {
    width: 500px;
    padding: 1rem;
    height: 575px;

    border: 1px solid black;
    overflow: hidden;
    background-color:#FFFFFF;
    margin: 3rem auto;
}
.admin-blog-list-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

   
}

.ownblogs-list-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
    width: 100%;
}
